import React from "react";

import {
  E_MapControlActions,
  T_Coordinates,
  T_SVGEventType,
  T_ViewBoxProps
} from "pages/Blogs/Travel/WorldMap/types";

export function getCompensatoryTranslationForZoom(
  zoomPoint: T_Coordinates,
  zoomFactor: number
): T_Coordinates {
  return {
    x: -(zoomPoint.x * (zoomFactor - 1)) / zoomFactor,
    y: -(zoomPoint.y * (zoomFactor - 1)) / zoomFactor
  };
}

// This function returns an object with X & Y values from the pointer event
export function getPointFromEvent(event: T_SVGEventType): T_Coordinates {
  const point = { x: 0, y: 0 };
  // If event is triggered by a touch event, we get the position of the first finger
  if (isTouchEvent(event)) {
    point.x = event.targetTouches[0].clientX;
    point.y = event.targetTouches[0].clientY;
  } else {
    point.x = event.clientX;
    point.y = event.clientY;
  }

  return point;
}

export function getElementRectMidPoint(
  svgRef: React.RefObject<SVGSVGElement>
): T_Coordinates | void {
  if (svgRef.current) {
    return {
      x: svgRef.current.getBoundingClientRect().width / 2,
      y: svgRef.current.getBoundingClientRect().height / 2
    };
  }
}

export function getWidthRatio(
  initialViewBox: T_ViewBoxProps,
  svgRef: React.RefObject<SVGSVGElement>
): number | void {
  if (svgRef.current) {
    return initialViewBox.width / svgRef.current.getBoundingClientRect().width;
  }
}

export function getElementTitleFromPoint(position: T_Coordinates): string {
  const element = document.elementFromPoint(position.x, position.y);

  if (element) {
    const dataObj = (element as any).dataset || element;
    return dataObj.title ?? dataObj.id;
  }

  return "";
}

function isTouchEvent(event: any): event is React.TouchEvent<SVGSVGElement> {
  return !!event?.targetTouches;
}
